import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-wizard';

const Wizard = ({ children, className, ...remainingProps }) => (
  <div className={classnames(`${CSS_PREFIX}-container`)}>
    <ul
      className={classnames(CSS_PREFIX, className)}
      {...remainingProps}
    >
      {children}
    </ul>
  </div>
);

Wizard.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

Wizard.displayName = 'Wizard';

export default Wizard;
