import React, { Fragment, useState } from 'react';
import ButtonIcon from '../../button-icon';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import ScreenReader from '../../screen-reader';
import Tooltip from '../../tooltip';
import Typography from '../../typography';
import Wizard from '../../wizard';
import WizardStep from '../../wizard-step';

const WizardExample = () => {
  const [state, setState] = useState({
    steps: [
      {
        active: true,
        completed: false,
        error: false,
        label: '1',
        title: 'Cart',
        tooltip: false
      },
      {
        active: false,
        completed: false,
        error: false,
        label: '2',
        title: 'Order Info',
        tooltip: false
      },
      {
        active: false,
        completed: false,
        error: false,
        label: '3',
        title: 'Shipping Info',
        tooltip: false
      },
      {
        active: false,
        completed: false,
        error: false,
        label: '4',
        title: 'Payment Info',
        tooltip: false
      },
      {
        active: false,
        completed: false,
        error: false,
        label: '5',
        title: 'Review',
        tooltip: false
      },
      {
        active: false,
        completed: false,
        error: false,
        label: '6',
        title: 'Confirmation',
        tooltip: false
      }]
  });

  const handleClickPrevious = () => {
    let index = state.steps.findIndex(step => step.active === true);
    index = index === -1 ? state.steps.length : index;
    const steps = state.steps.map((step, k) => ({
      ...step,
      active: k === index - 1,
      completed: k < index - 1, // use your own logic to determine completed state
      error: k <= index - 1 ? (k === 0 ? true : step.error) : false//, // set first item to error for demo
      // label: step.label,
      // tooltip: step.tooltip
    }));

    setState({ steps });
  };

  const handleClickNext = () => {
    const index = state.steps.findIndex(step => step.active === true);
    const steps = state.steps.map((step, k) => ({
      ...step,
      active: k === index + 1,
      completed: k <= index, // use your own logic/state to determine completed state
      error: k <= index ? (k === 0 ? true : step.error) : false//, // set first item to error for demo
      // label: step.label,
      // tooltip: step.tooltip
    }));

    setState({ steps });
  };

  const handleWizardTooltip = (step, value) => {
    const steps = state.steps.map((s, k) => ({
      ...s,
      tooltip: step === k ? value : s.tooltip
    }));

    setState({ steps });
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Wizard (BYO-State) Bring your own state</Typography>
        </Col>
        <Col textAlign="right">
          <ButtonText
            disabled={state.steps.findIndex(step => step.active === true) === 0}
            onClick={handleClickPrevious}
          >
            Previous
          </ButtonText>

          <ButtonText
            disabled={state.steps.findIndex(step => step.active === true) === -1}
            onClick={handleClickNext}
          >
            Next
          </ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Wizard>
            {
              state.steps.map((step, key) => (
                <WizardStep
                  key={key}
                  active={step.active}
                  completed={step.completed}
                  error={step.error}
                >
                  <Tooltip
                    tooltip={`Step ${step.label}`}
                    tooltipShown={step.tooltip}
                    placement="top"
                    trigger="none"
                    id={`stepper-${key}`}
                  >
                    <ButtonIcon
                      ariaLabel={step.label}
                      iconType="light-tiny"
                      onFocus={() => handleWizardTooltip(key, true)}
                      onBlur={() => handleWizardTooltip(key, false)}
                      onMouseEnter={() => handleWizardTooltip(key, true)}
                      onMouseLeave={() => handleWizardTooltip(key, false)}
                    >
                      <Fragment>
                        {
                          step.error ?
                            <ScreenReader>Error: {step.label}</ScreenReader>
                            : step.active ?
                              <ScreenReader>Current: </ScreenReader>
                              : step.completed ?
                                <ScreenReader>Completed: </ScreenReader>
                                : undefined
                        }
                        {
                          step.error ? '!' : step.label
                        }
                      </Fragment>
                    </ButtonIcon>
                  </Tooltip>
                </WizardStep>
              ))
            }
          </Wizard>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Wizard with step titles</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Wizard>
            {
              state.steps.map((step, key) => (
                <WizardStep
                  key={key}
                  active={key === 0}
                  title={step.title}
                >
                  <Tooltip
                    tooltip={`Step ${step.label}`}
                    placement="top"
                    trigger="none"
                    id={`stepper-${key}`}
                  >
                    <ButtonIcon
                      ariaLabel={step.label}
                      iconType="light-tiny"
                    >
                      {step.label}
                    </ButtonIcon>
                  </Tooltip>
                </WizardStep>
              ))
            }
          </Wizard>
        </Col>
      </Row>
    </Grid>
  );
};

export default WizardExample;
