import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-wizard-step';
const CSS_STATE = 'vds-state';

const WizardStep = forwardRef(({
  active,
  children,
  className,
  completed,
  error,
  title,
  ...remainingProps
}, ref) => (
  <li
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--completed`]: Boolean(completed),
      [`${CSS_STATE}--error`]: Boolean(error),
      [`${CSS_PREFIX}--titles`]: Boolean(title)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
    {title ?
      <span className={`${CSS_PREFIX}-title`}>
        {title}
      </span>
      : undefined
    }
  </li>
));

WizardStep.propTypes = {
  /**
  * Active
  */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Completed
  */
  completed: bool,

  /**
  * Error
  */
  error: bool,

  /**
  * Title
  */
  title: string


};

WizardStep.displayName = 'WizardStep';

export default WizardStep;
