import React, { cloneElement, Fragment, useRef } from 'react';
import { node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import TooltipTrigger from 'react-popper-tooltip';

const CSS_PREFIX = 'vds-tooltip';
const CSS_STATE = 'vds-state';

const Tooltip = ({ className, tooltip, children, id = shortid.generate(), trigger, ...remainingProps }) => {
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  return (
    <TooltipTrigger
      trigger={trigger}
      {...remainingProps}
      modifiers={{
        offset: {
          enabled: true,
          offset: '0, 4px'
        }
      }}
      tooltip={({ getTooltipProps, tooltipRef }) => (
        <div
          hidden
          {...getTooltipProps({
            className: classnames(CSS_PREFIX, `${CSS_STATE}--show`, className),
            ref: tooltipRef
          })}
        >
          <div className={`${CSS_PREFIX}-content`}>
            <span className={`${CSS_PREFIX}-text`}>{tooltip}</span>
            <span className={`${CSS_PREFIX}-pointer`} />
          </div>
        </div>
      )}
    >
      {({ getTriggerProps, triggerRef }) => (
        <Fragment>
          {cloneElement(children, {
            ...getTriggerProps({
              'aria-controls': uid,
              'aria-describedby': uid,
              ref: triggerRef
            })
          })}
          <span aria-hidden="true" id={uid} style={{ display: 'none' }}>
            {tooltip}
          </span>
        </Fragment>
      )}
    </TooltipTrigger>
  );
};
Tooltip.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Popper Placement
   */
  placement: oneOf([
    'auto',
    'bottom-end',
    'bottom-start',
    'bottom',
    'left-end',
    'left-start',
    'left',
    'right-end',
    'right-start',
    'right',
    'top-end',
    'top-start',
    'top'
  ]),

  /**
   * Tooltip
   */
  tooltip: node.isRequired,

  /**
   * Trigger
   */
  trigger: oneOf([
    'click',
    'hover',
    'right-click',
    'none'
  ])
};

Tooltip.displayName = 'Tooltip';

export default Tooltip;
